<template>
  <div class="statistical mescroll">
    <div class="top">
      <div class="flex">
        <div class="left">
          <p class="value">{{totalMoney.toFixed(2)}}</p>
          <p class="label">零售额</p>
        </div>
      </div>
    </div>
    <div class="mid">
      <div class="mid-top">
        <van-tabs v-model="active" :swipe-threshold="6" :title-active-color="'#ee0a24'" :border="false" @change="loadData">
          <van-tab v-for="(item,index) in cateList" :key="index" :title="item.cateName" />
        </van-tabs>
      </div>
      <div class="content">
        <div id="myChart"></div>
      </div>
    </div>
    <div class="bottom">
      <div class="week" @click="tap(index + 1)" v-for="(item,index) in chooseList" :key="index" 
      :style="(index + 1 == chooseList.length?'border-right:0;':'') + (index + 1 == type?'background:#ff9329;color:#fff;':'')">{{item}}</div>
    </div>
  </div>
</template>
<script>
import { Tab, Tabs } from "vant";
import echarts from "echarts";
import { getShopChart, getShopCateList } from "@/api";
export default {
  components: {
    [Tabs.name]: Tabs,
    [Tab.name]: Tab
  },
  data() {
    return {
      active: 0,
      totalMoney: 0,
      type: 1,
      chooseList: ["周", "月", "年"],
      cateList: [],
      list: []
    };
  },
  methods: {
    tap(type) {
      this.type = type;
      this.loadData();
    },
    drawLine() {
      // 基于准备好的dom，初始化echarts实例
      let myChart = echarts.init(document.getElementById("myChart"));
      let xAxisData = [],
        seriesData = [];
      this.list.map(item => {
        xAxisData.push(item.shopCode);
        seriesData.push(item.moneyNum);
      });
      // 绘制图表
      myChart.setOption({
        tooltip: {},
        xAxis: {
          data: xAxisData
        },
        yAxis: {},
        series: [
          {
            name: "销量",
            type: "line",
            data: seriesData
          }
        ]
      });
    },
    loadCate() {
      const loading = this.$toast.loading({
        duration: 0, // 持续展示 toast
        forbidClick: true // 禁用背景点击
      });
      getShopCateList().then(res => {
        loading.clear();
        if (res.code == 0) {
          this.cateList = res.data;
          this.loadData();
        } else {
          this.$toast(res.msg);
        }
      });
    },
    loadData() {
      getShopChart({
        cateCode: this.cateList[this.active].cateCode,
        type: this.type
      }).then(res => {
        if (res.code == 0) {
          this.list = res.data.list;
          this.totalMoney = res.data.totalMoney;
          this.drawLine();
        } else {
          this.$toast(res.msg);
        }
      });
    }
  },
  mounted() {
    this.loadCate();
  }
};
</script>
<style lang="scss" scoped>
.statistical {
  background: #fff;
  .top {
    background: url("../../assets/img/icon/account.png") no-repeat;
    background-size: 100% 100%;
    margin: 15px;
    border-radius: 5px;
    padding: 20px 0 10px;
    .flex {
      display: flex;
      padding: 15px;
      color: #fff;
      align-items: center;
      .left {
        flex: 1;
        text-align: center;
        .label {
          font-size: 13px;
          line-height: 40px;
        }
        .value {
          font-size: 30px;
          line-height: 40px;
        }
      }
      .right {
        flex: 1;
        .btn {
          width: 80px;
          height: 35px;
          line-height: 35px;
          border-radius: 5px;
          background: #fff;
          color: #eb5252;
          float: right;
          text-align: center;
          font-size: 13px;
          font-weight: bold;
        }
      }
    }
  }
  .mid {
    margin-top: 25px;
    &-top {
      border-bottom: 1pt solid #efefef;
      box-sizing: border-box;
      position: relative;
      z-index: 1;
    }
    .content {
      #myChart {
        width: 100vw;
        margin: -30px auto 0;
        height: calc(100vh - 140px - 44px - 40px + 20px);
      }
    }
  }
  .bottom {
    display: flex;
    width: 100vw;
    position: fixed;
    bottom: 0;
    left: 0;
    div {
      flex: 1;
      text-align: center;
      line-height: 40px;
      border-top: 1pt solid #efefef;
      box-sizing: border-box;
      font-size: 14px;
    }
    .week,
    .month {
      border-right: 1pt solid #efefef;
      box-sizing: border-box;
    }
  }
}
</style>
